import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import * as moment from 'moment';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { LookupKeyDescConstants } from 'src/app/core/constants/lookupKeyDesc.const';
import { ForecastEntryModel } from 'src/app/main/mps-mrp/models/forecast-import-export.model';
import { ForecastImportExportService } from 'src/app/main/mps-mrp/http/forecast-import-export.service';
import { ModalService } from 'src/app/core/services/modal.service';
import { GridComponent } from '@progress/kendo-angular-grid';
import * as XLSX from 'xlsx';

@Component({
  selector: 'app-forecast-import-export',
  templateUrl: './forecast-import-export.component.html',
  styleUrls: ['./forecast-import-export.component.scss']
})
export class ForecastImportExportComponent implements OnInit {
  public mstabType: string = 'TAB1';
  public mslookupscreen: string = 'Forecast Entry';
  public mbIsLoading: boolean = false;
  public mobjModel: ForecastEntryModel = new ForecastEntryModel();
  public msCustomDateFormat: string = '';
  public msScreenName: string = 'Forecast Import Export';
  public mobjUserData: any = {};
  public msForecastKey: string = '';
  public msFilePath: string = '';

  public mobjLookupDataModel: ForecastEntryModel = new ForecastEntryModel();
  public marrLookupGridData: any = [];
  public mobjLookupKeyData: any = {};
  public msLookupField: string = "";
  public mbIsLookupModal: boolean = false;

  public msLookupCalendar: string = 'Calendar Lookup';
  public msLookupItemFrom: string = 'Item From Lookup';
  public msItemFromDescription: string = '';
  public msLookupItemTo: string = 'Item To Lookup';
  public msItemToDescription: string = '';
  public msLookupLocationFrom: string = 'Location From Lookup';
  public msLocationFromDesc: string = '';
  public msLookupLocationTo: string = 'Location To Lookup';
  public msLocationToDesc: string = '';


  public fileEvent: any;
  public msReportDtl: string = '';
  public msReportName: string = '';

  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/

  constructor(private route: ActivatedRoute,
    public router: Router,
    private formService: ForecastImportExportService,
    private lookupService: LookupService,
    private toastMessageService: ToastMessageService,
    private dateFormatService: DateFormatService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService, private lsService: LocalStorageService,
    private modalservice: ModalService) { }

  ngOnInit(): void {
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.mobjModel.STARTDATE = new Date();
    this.msReportName = "ForecastTemp" + moment(this.mobjModel.STARTDATE).format('DDMMYY') + ".XLSX";
  }

  bmlfonDateChange() {
    if (this.mobjModel.STARTDATE == null) {
      this.mobjModel.STARTDATE = new Date();
    }
  }

  bmlfswitchTab(TabKey) {
    this.mstabType = TabKey;
  }

  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      return false;
    }
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      this.mbIsLoading = false;
      return false;
    }

    // if (psLookUpKey == 'LocationFrom') {
    //   psScreenName = 'Run MPS';
    // }  Bug 185577 - As per Discussion with Kavita Mam Change Lookup Details 29-1-2024

    let psReqData: any;
    if (psLookUpKey == "Itemkey") {
      psReqData = { "ScreenName": psScreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }
    // else if(psField == 'Location From Lookup'){
    //   psReqData = { "ScreenName": psScreenName, "LookUpKey": psLookUpKey, "Filter1": this.mobjModel.FromItem, "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    // }
    // else if(psField == 'Location To Lookup'){
    //   psReqData = { "ScreenName": psScreenName, "LookUpKey": psLookUpKey, "Filter1": this.mobjModel.ToItem, "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    // }
    // Bug 185577 - Change in Location Lookup Query 12-2-24
    else if(psLookUpKey == 'Location'){
      psReqData = { "ScreenName": 'Forecast Export Import', "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }
    // Bug 189978 - Add For Planning Calender Lookup
    else if(psLookUpKey == 'PlanningCalendar'){
      psReqData = { "ScreenName": this.mslookupscreen, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }
    this.lookupService.getLookup(psReqData).subscribe({
      next: (res) => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      let obj1: any = {};
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        //Bug 185557 Add Date Formate Code
        res.LookupData.forEach(ele => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          })
        });
        this.marrLookupGridData = res.LookupData;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.title = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          let pobjFirstInput;
          pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];

          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else if (parrFilterdArr.length == 0) {
            let psmodelKey = this.bmlfGetModelField(psField, 0);
            this.mobjModel[psmodelKey] = ''
            this.mbIsLookupModal = true;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, error: (e) => {
      this.mbIsLoading = false;
      console.log(e);
      this.mbIsLoading = false;
    }
    });
  }

  bmlfSaveLookupField(pobjEvent) {
    debugger
    this.mbIsLookupModal = false;
    this.mbIsLookupModal = false;
    if (pobjEvent.field == this.msLookupCalendar) {
      this.mobjModel.CALENDARKEY = pobjEvent.value["CalendarKey"].trim();
    } else if (pobjEvent.field == this.msLookupItemFrom) {
      this.mobjModel.FromItem = pobjEvent.value["itemkey"].trim();
      this.msItemFromDescription = pobjEvent.value["Desc1"].trim();
    } else if (pobjEvent.field == this.msLookupItemTo) {
      this.mobjModel.ToItem = pobjEvent.value["itemkey"].trim();
      this.msItemToDescription = pobjEvent.value["Desc1"].trim();
    } else if (pobjEvent.field == this.msLookupLocationFrom) {
      this.mobjModel.FromLocation = pobjEvent.value["Location"].trim();
      this.msLocationFromDesc = pobjEvent.value["Description"].trim();
    } else if (pobjEvent.field == this.msLookupLocationTo) {
      this.mobjModel.ToLocation = pobjEvent.value["Location"].trim();
      this.msLocationToDesc = pobjEvent.value["Description"].trim();
    }
    this.bmlfonChange(pobjEvent.field);
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate(psMsg));
  }

  // Set Lookup Value Column
  bmlfGetInputColumn(psfield) {
    if (psfield == this.msLookupItemFrom) {
      return 'itemkey';
    } else if (psfield == this.msLookupItemTo) {
      return 'itemkey';
    } else if (psfield == this.msLookupLocationFrom) {
      return 'Location';
    } else if (psfield == this.msLookupLocationTo) {
      return 'Location';
    } else if (psfield == this.msLookupCalendar) {
      return 'CalendarKey';
    }
  }
  //Return Model Field
  bmlfGetModelField(psfield, rowIndex) {
    if (psfield == this.msLookupItemFrom) {
      return 'FromItem';
    } else if (psfield == this.msLookupItemTo) {
      return 'ToItem';
    } else if (psfield == this.msLookupLocationFrom) {
      return 'FromLocation';
    } else if (psfield == this.msLookupLocationTo) {
      return 'ToLocation';
    } else if (psfield == this.msLookupCalendar) {
      return 'CALENDARKEY';
    }
  }

  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        let psModelField = this.bmlfGetModelField(pobjData.field, pobjData.eleId);
        this.mobjModel[psModelField] = '';
        this.bmlfonChange(pobjData.field);
      }
      document.getElementById(pobjData.eleId).focus();
    }
  }

  bmlfonChange(psField, pseleId: string = "") {
    this.mbIsLoading = false;
    if (psField == this.msLookupItemFrom) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.FromItem)) {
        this.msItemFromDescription = '';
      } else if (this.mobjModel.ToItem != '') {
        if (this.mobjModel.FromItem > this.mobjModel.ToItem) {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('FORECASTIE.VALMSG')))
          this.mobjModel.FromItem = "";
          this.msItemFromDescription = "";
        }
      }
    } else if (psField == this.msLookupItemTo) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.ToItem)) {
        this.msItemToDescription = '';
      } else if (this.mobjModel.FromItem != '') {
        if (this.mobjModel.FromItem > this.mobjModel.ToItem) {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('FORECASTIE.VALMSG')))
          this.mobjModel.ToItem = "";
          this.msItemToDescription = "";
        }
      }
    } else if (psField == this.msLookupLocationFrom) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.FromLocation)) {
        this.msLocationFromDesc = '';
      } else if (this.mobjModel.ToLocation != '') {
        if (this.mobjModel.FromLocation > this.mobjModel.ToLocation) {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('FORECASTIE.VALMSG')))
          this.mobjModel.FromLocation = "";
          this.msLocationFromDesc = "";
        }
      }
    } else if (psField == this.msLookupLocationTo) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.ToLocation)) {
        this.msLocationToDesc = '';
      } else if (this.mobjModel.FromLocation != '') {
        if (this.mobjModel.FromLocation > this.mobjModel.ToLocation) {
          this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('FORECASTIE.VALMSG')))
          this.mobjModel.ToLocation = "";
          this.msLocationToDesc = "";
        }
      }
    }
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = '';
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  bmlfViewHelpDoc() {
    window.open('../../../../../../assets/help files/Forecast Export Import.pdf', '_blank');
  }

  bmlfFooterAction(key) {
    if (key == 'CLOSE') {
      this.router.navigate(['/main/dashboard/dashboardpage']);
    } else if (key == 'EXPORT') {
      if (this.mobjModel.CALENDARKEY == '') {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('FORECASTIE.SELECTCALENDAR'));
        return;
      }
      this.bmlfExportData();
    } else if (key == 'IMPORT') {
      if (this.msForecastKey == '') {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('FORECASTIE.ENTERFORECAST'));
        return;
      }
      this.bmlfImportData();
    }
  }

  bmlfExportData() {
    let parrGridData: any = [];
    let psServeDateFormat: string = CommonConstants.gsServerDateFormat;
    this.mobjModel.STARTDATE = moment(this.mobjModel.STARTDATE).format(psServeDateFormat);
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ ... this.mobjModel }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };

    this.formService.BMGFExportForecastDetails(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
      }
      if (res.Response[0].ErrorCode == "") {
        if (res.Table.length > 0) {
          parrGridData = res.Table;
          parrGridData.forEach(ele => {
            Object.keys(ele).forEach((key) => {
              if (ele[key] == null) {
                ele[key] = "";
              }
            });
          });
          this.formService.exportAsExcelFile(parrGridData, this.msReportName);
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('FORECASTIE.DATAEXPORT'));
        } else {
          this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATAFOUND'));
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  onFileChange(ev) {
    this.fileEvent = ev;
    this.msFilePath = this.fileEvent.target.files[0].name;
  }

  bmlfImportData() {
    if (this.fileEvent == null || this.fileEvent == undefined) {
      return;
    }
    this.formService.ImportExcelToJson(this.fileEvent).then((data: any) => {
      this.bmlfSaveData(data);
    }, err => {
      console.log(err);
    });   
  }

  bmlfSaveData(JsonData) {
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          forecastKey: this.msForecastKey,
        }],
        DetailsData: JsonData,
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.formService.BMGFImportForecastDetails(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('FORECASTIE.DATAIMPORT'));
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });

  }

}
