  bmlfonDeleteLineItem(item) {
    debugger
    if (!this.appUtility.mbDelete) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    //  scr  - 54723 Metarom USA by NISHA GUPTA 8-1-2024 
    // change item in place rowindex because rowindex is not getting right from html
    if(this.mArrGrid1.length>0){
      this.mArrGrid1.forEach((ele,i)=>{
        if(ele.MPS_Item == item.MPS_Item && ele.Location == item.Location){
          this.miGridDeleteLineIndex = i;
        }
       else if(item.MPS_Item == '' &&  item.Location == ''){
          this.miGridDeleteLineIndex = 0;
        }
      })
    }
    this.mbdeleteLineItem = true;
  }

  bmlfdialogDeleteLineAction(action: any) {
    if (action == 'Yes') {
      this.mbdeleteLineItem = false;
      let psFlag: string = this.mArrGrid1[this.miGridDeleteLineIndex].Flag;
      if (psFlag == "A") {
        //  scr  - 54723 Metarom USA by NISHA GUPTA 8-1-2024 
        let parrTempData = this.mArrGrid1.map(x => Object.assign({}, x));
        parrTempData.splice(this.miGridDeleteLineIndex, 1);
        this.mArrGrid1 = parrTempData;
        return;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            MPS_Item: this.mArrGrid1[this.miGridDeleteLineIndex].MPS_Item,
            Location: this.mArrGrid1[this.miGridDeleteLineIndex].Location,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfDeleteMPSItems(reqData).subscribe(res => {
        if (res == null) {
          return;
        }
        if (res.Response[0].ErrorCode != "") {
          this.objToastService.notifyError(res.Response[0].ResponseText);
        } else {
          this.objToastService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
          this.mArrGrid1.splice(this.miGridDeleteLineIndex, 1);
          this.bmlfGetMPSItem();
        }
      });
    }
    if (action == 'No') {
      this.mbdeleteLineItem = false;
    }
  }

