  bmlfRedirectFunctionality() {
    if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.User1)) {
      let queryParamsObj = { queryParams: {} };
      queryParamsObj.queryParams[this.mbHyerLinkData.data.User2] = this.mbHyerLinkData.msField1Value;
      if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.KeyValue2)) {
        queryParamsObj.queryParams[this.mbHyerLinkData.data.User3] = this.mbHyerLinkData.msField2Value;
      }
      this.router.navigate([this.mbHyerLinkData.data.User1], queryParamsObj);
    }
  }
  bmlfGetDecimal() {
    let pobjReqData = {
      ScreenName: 'MaintainMPSItemComponent',
      LookUpKey: 'DecimalPrecision'
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length) {
        let parrSalesDefaultQtyDes = res.LookupData;
        parrSalesDefaultQtyDes.forEach(ele => {
          this.miPrecision = ele.Default_Value || 2;
          let formatType = typeof ele.Default_Value == 'number' ? String(ele.Default_Value) : ele.Default_Value;
          this.msPrecisionFormat = 'n' + formatType || '2';
        });
      }
    });
  }

  bmlfSetItemTypeifBOMExist(rowIndex, GridNo) {
    let ItemKey: string;
    let Location: string;
    if (GridNo == 1) {
      ItemKey = this.mArrGrid1[rowIndex].MPS_Item;
      Location = this.mArrGrid1[rowIndex].Location;
    } else {
      ItemKey = this.mArrGrid2[rowIndex].MPS_Item;
      Location = this.mArrGrid2[rowIndex].Location;
    }
    let pobjReqData = {
      ScreenName: 'MaintainMPSItemComponent',
      LookUpKey: 'BOMDetails',
      Filter1: ItemKey,
      Filter2: Location
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        if (GridNo == 1) {
          this.mArrGrid1[rowIndex].Item_Type = 'M';
          this.mArrGrid1[rowIndex]['Default_Vendor'] = '';
          this.mArrGrid1[rowIndex]['VendorName'] = '';
        } else {
          this.mArrGrid2[rowIndex].Item_Type = 'Manufacture';
        }
      } else {
        this.bmlfGetLookupDes(rowIndex);
      }
    });
  }

  bmlfNoItemBOMExist(item, GridNo) {
    debugger
    let ItemKey: string;
    let Location: string;
    let ItemType: string;
    //  scr  - 54723 Metarom USA by NISHA GUPTA 8-1-2024 
    let rowIndex=0;
    this.mArrGrid1.forEach((ele,i)=>{
      if(ele.MPS_Item == item.MPS_Item && ele.Location == item.Location){
        rowIndex = i;
      }
      else if(item.MPS_Item == '' && item.Location == ''){
        rowIndex = 0;
      }
    })
    if (GridNo == 1) {
      ItemKey = item.MPS_Item;
      Location = this.mArrGrid1[rowIndex].Location;
      ItemType = this.mArrGrid1[rowIndex].Item_Type;
      this.mbChange = true;
      if (this.mArrGrid1[rowIndex]['Flag'] == '') {
        this.mArrGrid1[rowIndex]['Flag'] = 'M';
      }
    } else {
      ItemKey = this.mArrGrid2[rowIndex].MPS_Item;
      Location = this.mArrGrid2[rowIndex].Location;
      ItemType = this.mArrGrid2[rowIndex].Item_Type;
    }
    if (ItemType == 'M' || ItemType == 'Manufacture') {
      let pobjReqData = {
        ScreenName: 'MaintainMPSItemComponent',
        LookUpKey: 'BOMDetails',
        Filter1: ItemKey,
        Filter2: Location
      }
      this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
        if (res.LookupData.length == 0) {
          if (GridNo == 1) {
            this.mArrGrid1[rowIndex].Item_Type = 'B';
          } else {
            this.mArrGrid2[rowIndex].Item_Type = 'Purchase';
          }
          this.objToastService.notifyError((this.appUtility.bmgfonTranslate('MAINTAINMPSITEM.MSG.BOMEXIST')) + ': Row No. ' + Number(rowIndex + 1));
        }
      });

    }
  }

  bmlfGetVendorSource() {
    let pobjReqData = {
      ScreenName: 'MaintainMPSItemComponent',
      LookUpKey: 'VendorSource'
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mnVendorSource = Number(res.LookupData[0].VendorSource);
      }
    });
  }

  SetGridColumns(GridName, parrGridCol) {
    this.appUtility.GetJsonSetting(this.msScreenName, this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
      if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
        parrGridCol = pArrGridChangedCol
      }
    }, err => {
      console.log(err);
    });
  }

