import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { HttpHeaders } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class RunMpsService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfGenerateMPS(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFGenerateMPS`;
    return this.httpExt.post<any>(url, reqdata, null);
  }
}
