import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class ForecastEntryService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteForecastEntry(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteForecast`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveForecastEntry(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFInsertForecast`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdateForecast`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetForecastEntry(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetForecastEntry`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfGetUomUnit(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetItemUOMConversion`;
        return this.httpExt.post(url, reqdata, null);
    }
    public bmlfGetInitialForecast(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFInitialForecastDetails`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfDeleteForecastLine(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteForecastLine`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfChangeForecast(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFChangeForecast`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfInsertForecast(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFInsertForecastDetails`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfFillForecast(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFFillForecastDetails`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}