import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { ODataModel } from 'src/app/core/models/odata.model';


@Injectable({
  providedIn: 'root'
})
export class EoqService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfSave(reqdata, formMode): Observable<any> {
    if (formMode == "I") {
      const url = `${this.appSettings.ServerAPIEndpointnew}/bme/CreateEOQ`;
      return this.httpExt.post<any>(url, reqdata, null);
    } else {
      const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateEOQ`;
      return this.httpExt.post(url, reqdata, null);
    }
  }

  public bmlfGet(headerData) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetEOQ`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public bmlfDelete(headerData) {
    debugger
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteEOQ`;
    return this.httpExt.post(url, headerData, null);
  }

  public bmlfDeleteLine(headerData) {
    debugger
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteEOQLine`;
    return this.httpExt.post(url, headerData, null);
  }
}
