import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { Observable } from 'rxjs';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';
import { HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MaintainMpsItemService {

  constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) { }

  public bmlfPullMPSItemFromInloc(headerData) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFPullMPSItemFromInloc`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public bmlfGetMPSItem(headerData) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetMPSItem`;
    return this.httpExt.post<any>(url, headerData, null);
  }

  public bmlfSavePulledItems(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFSavePulledItems`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfUpdateMPSItems(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFUpdateMPSItems`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfDeleteMPSItems(reqdata): Observable<any> {
    const url = `${this.appSettings.ServerAPIEndpointnew}/bme/BMGFDeleteMPSItems`;
    return this.httpExt.post<any>(url, reqdata, null);
  }

  public bmlfGetAllMPSItem(headerData) {
    const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetAllMPSItem`;
    return this.httpExt.post<any>(url, headerData, null);
  }

}
