import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class CompanyCalendarService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfGetCompanyCalendar(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetCompanyCalendar`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveCompanyCalendar(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/SaveCompanyCalendar`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}