import { Component, OnInit } from '@angular/core';
import { locale as english } from './i18n/en';
import { locale as hindi } from './i18n/hi';

import { locale as Chinese } from './i18n/zh';
import { locale as French } from './i18n/fr';
import { locale as Spanish } from './i18n/es';
import { locale as Portuguese } from './i18n/pt';
import { locale as Vietnamese } from './i18n/vi';

import { LanTranslationLoaderService } from 'src/app/core/services/translation-loader.service';

@Component({
  selector: 'app-mps-mrp',
  templateUrl: './mps-mrp.component.html',
  styleUrls: ['./mps-mrp.component.scss']
})
export class MpsMrpComponent implements OnInit {

  constructor(
    private lanTranslationLoaderService: LanTranslationLoaderService
  ) {

    this.lanTranslationLoaderService.loadTranslations(english, hindi, Chinese, French, Spanish, Portuguese, Vietnamese );
  }

  ngOnInit(): void {
  }

}
