import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '../../../../../core/services/alert.service';
import { ToastMessageService } from '../../../../../core/services/toast-message.service';
import { AppUtility } from '../../../../../core/services/utility.services';
import { LookupService } from '../../../../../shared/http/lookup.service';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { ModalService } from 'src/app/core/services/modal.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { RunMPSModel } from '../../../models/run-mps.model';
import { RunMpsService } from '../../../http/run-mps.service';


@Component({
  selector: 'app-run-mps',
  templateUrl: './run-mps.component.html',
  styleUrls: ['./run-mps.component.scss']
})
export class RunMpsComponent implements OnInit {
  public msscreenName: string = 'Run MPS';
  public mbIsLookupModal: boolean = false;
  public mbIsLoading: boolean = false;
  public mbloaderEnabled: boolean = false;
  public marrLookupGridData: any = [];
  public mobjLookupKeyData: any = {};
  public msLookupField: string = "";
  public mnumLineItemRowIndex: number = 0;
  public msCustomDateFormat: string = "";
  public mobjUserData: any = {};
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  public mbReportDialog: boolean = false;
  public msReportDtl: string = ''
  public mbShowDialog: boolean = false;

  public mobjModel: RunMPSModel = new RunMPSModel();
  public mobjModelBlank: RunMPSModel = new RunMPSModel();
  public mobjLookupDataModel: RunMPSModel = new RunMPSModel();

  public msLookupItemFrom: string = 'Item Key From Lookup';
  public msItemFromDescription: string = '';
  public msLookupItemTo: string = 'Item Key To Lookup';
  public msItemToDescription: string = '';
  public msLookupLocationFrom: string = 'Location From Lookup';
  public msLocationFromDesc: string = '';
  public msLookupLocationTo: string = 'Location To Lookup';
  public msLocationToDesc: string = '';
  public mdRunDate: Date = new Date();
  public mbisSkipCoading: boolean = false;

  constructor(public router: Router, private dtService: DateFormatService, private objService: RunMpsService,
    private lookupService: LookupService, private lsService: LocalStorageService, private objToastService: ToastMessageService,
    public dateFormatService: DateFormatService, public objUtils: AppUtility,
    public userFieldService: UserFieldService, private route: ActivatedRoute, public modalservice: ModalService) {
  }

  ngOnInit(): void {
    this.objUtils.getBMEScreePermission(this.msscreenName);
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.mobjModel.GeneratePlannedOrderAs = "Planned";
    this.mobjModel.EndDate.setMonth(new Date().getMonth() + 1);
  }

  //Lookup
  bmlfonChange(psField, pseleId: string = "") {
    this.mbIsLoading = false;
    if (psField == this.msLookupItemFrom) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.ItemKeyFrom)) {
        this.msItemFromDescription = '';
      } else if (this.mobjModel.ItemKeyTo != '') {
        if (this.mobjModel.ItemKeyFrom > this.mobjModel.ItemKeyTo) {
          this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RUNMPS.VALMSG')))
          this.mobjModel.ItemKeyFrom = "";
          this.msItemFromDescription = "";
        }
      }
    } else if (psField == this.msLookupItemTo) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.ItemKeyTo)) {
        this.msItemToDescription = '';
      } else if (this.mobjModel.ItemKeyFrom != '') {
        if (this.mobjModel.ItemKeyFrom > this.mobjModel.ItemKeyTo) {
          this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RUNMPS.VALMSG')))
          this.mobjModel.ItemKeyTo = "";
          this.msItemToDescription = "";
        }
      }
    } else if (psField == this.msLookupLocationFrom) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.LocationFrom)) {
        this.msLocationFromDesc = '';
      } else if (this.mobjModel.LocationTo != '') {
        if (this.mobjModel.LocationFrom > this.mobjModel.LocationTo) {
          this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RUNMPS.VALMSG')))
          this.mobjModel.LocationFrom = "";
          this.msLocationFromDesc = "";
        }
      }
    } else if (psField == this.msLookupLocationTo) {
      if (CustomValidator.bmgfNullCheckValidator(this.mobjModel.LocationTo)) {
        this.msLocationToDesc = '';
      } else if (this.mobjModel.LocationFrom != '') {
        if (this.mobjModel.LocationFrom > this.mobjModel.LocationTo) {
          this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RUNMPS.VALMSG')))
          this.mobjModel.LocationTo = "";
          this.msLocationToDesc = "";
        }
      }
    }
  }

  bmlfonDateChange() {
    if (!CustomValidator.bmgfCompareTwoDatesWithEqual(this.mobjModel.StartDate, this.mobjModel.EndDate)) {
      this.mobjModel.EndDate = new Date();
      this.mobjModel.EndDate.setMonth(new Date().getMonth() + 1);
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('RUNMPS.DATEMSG')));
      return false;
    }
  }

  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      return false;
    }
    psScreenName = psScreenName.toUpperCase();
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      this.mbIsLoading = false;
      return false;
    }

    let psReqData: any;
    if (psLookUpKey != "") {
      psReqData = { "ScreenName": psScreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    };

    this.lookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      let obj1: any = {};
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.title = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          let pobjFirstInput;
          pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];

          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfSaveLookupField(pobjEvent) {
    debugger
    this.mbIsLookupModal = false;
    if (pobjEvent.field == this.msLookupItemFrom) {
      this.mobjModel.ItemKeyFrom = pobjEvent.value["ItemKey"].trim();
      this.msItemFromDescription = pobjEvent.value["Desc1"].trim();
    } else if (pobjEvent.field == this.msLookupItemTo) {
      this.mobjModel.ItemKeyTo = pobjEvent.value["ItemKey"].trim();
      this.msItemToDescription = pobjEvent.value["Desc1"].trim();
    } else if (pobjEvent.field == this.msLookupLocationFrom) {
      this.mobjModel.LocationFrom = pobjEvent.value["Location"].trim();
      this.msLocationFromDesc = pobjEvent.value["Description"].trim();
    } else if (pobjEvent.field == this.msLookupLocationTo) {
      this.mobjModel.LocationTo = pobjEvent.value["Location"].trim();
      this.msLocationToDesc = pobjEvent.value["Description"].trim();
    }
    this.bmlfonChange(pobjEvent.field);
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate(psMsg));
  }

  // Set Lookup Value Column
  bmlfGetInputColumn(psfield) {
    if (psfield == this.msLookupItemFrom) {
      return 'ItemKey';
    } else if (psfield == this.msLookupItemTo) {
      return 'ItemKey';
    } else if (psfield == this.msLookupLocationFrom) {
      return 'Location';
    } else if (psfield == this.msLookupLocationTo) {
      return 'Location';
    }
  }
  //Return Model Field
  bmlfGetModelField(psfield, rowIndex) {
    if (psfield == this.msLookupItemFrom) {
      return 'ItemKeyFrom';
    } else if (psfield == this.msLookupItemTo) {
      return 'ItemKeyTo';
    } else if (psfield == this.msLookupLocationFrom) {
      return 'LocationFrom';
    } else if (psfield == this.msLookupLocationTo) {
      return 'LocationTo';
    }
  }

  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        let psModelField = this.bmlfGetModelField(pobjData.field, pobjData.eleId);
        this.mobjModel[psModelField] = '';
        this.bmlfonChange(pobjData.field);
      }
      document.getElementById(pobjData.eleId).focus();
    }
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.objUtils.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.objUtils.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msscreenName;
      pobjFormRec.primeKey = '';
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  bmlfViewHelpDoc() {
    window.open('../../../../../../assets/help files/Run MPS.pdf', '_blank');
  }

  bmlfonClose() {
    this.router.navigate(['/main/dashboard/dashboardpage']);
  }

  bmlfonProcess() {
    this.mbShowDialog = true;
  }

  bmlfConfirmProcess() {
    let pobjData = Object.assign({}, this.mobjModel);
    if (this.mobjModel.SkipLowlevelcoding == 1) {
      pobjData.SkipLowlevelcoding = true;
    } else {
      pobjData.SkipLowlevelcoding = false;
    }

    pobjData.StartDate = moment(new Date(this.mobjModel.StartDate)).format(CommonConstants.gsServerDateFormat.toUpperCase());
    pobjData.EndDate = moment(new Date(this.mobjModel.EndDate)).format(CommonConstants.gsServerDateFormat.toUpperCase());

    let reqData: any = {
      JSON: JSON.stringify({
        Details: [{ ...pobjData }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfGenerateMPS(reqData).subscribe(res => {
      if (res == null) {
        this.mbIsLoading = false;
        return;
      }
      if (res.Response[0].ErrorCode == "") {
        this.msReportDtl = this.objUtils.bmgfonTranslate(res.Response[0].ResponseText);
        if (this.msReportDtl.length > 80) {         
          this.bmlfReportModal('open');
        }
        else {
          this.objToastService.notifySuccess(res.Response[0].ResponseText);
        }
      } else {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      }
      this.mbIsLoading = false;
    }, err => {
      this.mbIsLoading = false;
      console.log(err);
    });
  }

  bmlfReportModal(psKey) {
    debugger
    if (psKey == 'open') {
      this.mbReportDialog = true;
    } else if (psKey == 'close' || psKey == 'ok') {
      this.mbReportDialog = false;
    }
  }


  bmlfonDialogAction(action) {
    if (action == 'No') {
      this.mbShowDialog = false;
      return;
    } //Yes
    else if (action == 'Yes') {
      this.mbShowDialog = false;
      this.bmlfConfirmProcess();
    } else {
      this.mbShowDialog = false;
    }
  }
}
