  //------------------------------------------------------------------------------------------------
  bmlfonValidateData() {
    let pErrormessagge = "";
    let pbSuccess: boolean = true;
    if (this.mObjModel.ItemKey.trim() == "") {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.ENTERITEM'));
      return false;
    }
    if (this.mObjModel.Location.trim() == "") {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.ENTERLOCATION'));
      return false;
    }

    if (this.mArrGrid1.length == 0) {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.EOQLINE'));
      return false;
    }

    //Check Duplicate row
    let pbBlank = false;
    let pbDuplicate = false;
    let piRowindex: number = 1;
    let piBlankRowindex: number = 0;
    let piDupRowindex: number = 0;

    this.mArrGrid1.forEach(ele => {
      if (Number(ele.EOQ) == 0 && pbBlank == false) {
        pbBlank = true;
        piBlankRowindex = piRowindex;
      };
      if (pbDuplicate == false) {
        let parrExist = this.mArrGrid1.filter(x => x.EOQ == ele.EOQ);
        if (parrExist.length > 1) {
          pbDuplicate = true;
          piDupRowindex = piRowindex;
        }
      }
      ++piRowindex;
    });

    if (pbBlank) {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.EOQVALUE') + ':' + piBlankRowindex);
      return false;
    };
    if (pbDuplicate) {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.DUPEOQ') + ':' + piDupRowindex);
      return false;
    };
    
    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.objToastService.notifyWarning(resultString.msg);
      return false;
    }

    return true;
  }

  bmlfSetModFlag() {
    this.mArrGrid1.forEach(ele => {
      ele.Flag = 'M';
    });
  }

  // Add Line Item Grid 1
  bmlfonAddLineGrid() {
    
    if (this.mObjModel.ItemKey.trim() == "") {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.ENTERITEM'));
      return false;
    }
    if (this.mObjModel.Location.trim() == "") {
      this.objToastService.notifyError(this.objUtils.bmgfonTranslate('EOQTY.MSG.ENTERLOCATION'));
      return false;
    }

    if (this.bmlfChkBlankLine()) {
      return false;
    }

    const pobjDataCopy: any = Object.assign({}, this.mObjModelBlank);
    let piLastIndex;
    piLastIndex = this.mArrGrid1.length;
    pobjDataCopy.Flag = 'A';
    pobjDataCopy.ItemKey = this.mObjModel.ItemKey;
    pobjDataCopy.Location = this.mObjModel.Location;
    pobjDataCopy.Seq_No = piLastIndex + 1;
    this.mArrGrid1.push(pobjDataCopy);
    this.mbChange = true;
  }
