  bmlfChangeForecastDate() {
    if (this.msFormMode == "I" && this.marrGridData.length == 0) {
      this.bmlfGetInitialForecastData();
    } else if (this.msFormMode == "U") {
      this.mbIsChangeForecastDate = true;
    }
  }
  bmlfChangeForeCastType() {
    if (this.mobjForecastModel.ForecastType == "Q") {
      this.mobjForecastModel.CostMethod = "";
      this.miPrecision = this.miQtyPrecision;
      this.miPrecisionFormat = this.miQtyPrecisionFormat
    } else {
      this.mobjForecastModel.CostMethod = "Base";
      this.miPrecision = this.miAmtPrecision;
      this.miPrecisionFormat = this.miAmtPrecisionFormat
    }
  }
  bmlfCalculateMeasureUnit() {
    this.marrGridData.forEach((element, index) => {
      this.bmlfChangeItemUnitEvent(index, element);
    });
    this.msOldUnit = this.mobjForecastModel.UOM;
  }
  bmlfChangeItemUnitEvent(pnumIndex: number, element: any): void {
    
    let pobjData = Object.assign({}, element);
    let parrFilterUnitData = this.marrItemUnitList.filter(x => {
      if (x.ToKey == this.mobjForecastModel.UOM && x.Itemkey == this.mobjForecastModel.ItemKey) {
        return x;
      }
    });

    /** --- get unit conversation acc to old unit--- */
    let parrOldData = this.marrItemUnitList.filter(x => {
      if (x.ToKey == this.msOldUnit && x.Itemkey == this.mobjForecastModel.ItemKey) {
        return x;
      }
    });
    if (parrOldData.length > 0) {
      /**---- if  unit and old  unit is same */
      if (parrOldData[0].FromKey == this.msOldUnit) {
        Object.keys(pobjData).forEach(key => {
          if ((key.includes("/") || key.includes("-")) && typeof pobjData[key] == 'number') {
            pobjData['old' + key] = pobjData[key];
          }
        })
      } else {  // if not same  then reverse unit conversation for achieve initial value
        if (parrOldData[0].Operation == "1") {
          Object.keys(pobjData).forEach(key => {
            if ((key.includes("/") || key.includes("-")) && typeof pobjData[key] == 'number') {
              pobjData['old' + key] = Number(pobjData[key]) > 0 ? this.utilService.bmgfSetPrecision((1 / parrOldData[0].convfctr) * Number(pobjData[key]), this.miQtyPrecision) : this.utilService.bmgfSetPrecision(0, this.miQtyPrecision);
            }
          })
        } else {
          Object.keys(pobjData).forEach(key => {
            if ((key.includes("/") || key.includes("-")) && typeof pobjData[key] == 'number') {
              pobjData['old' + key] = Number(pobjData[key]) > 0 ? this.utilService.bmgfSetPrecision(parrOldData[0].convfctr * Number(pobjData[key]), this.miQtyPrecision) : this.utilService.bmgfSetPrecision(0, this.miQtyPrecision);
            }
          })
        }
      }
    }
    if (parrFilterUnitData.length > 0) {
      if (parrFilterUnitData[0].Operation == "2") {
        Object.keys(pobjData).forEach(key => {
          if ((key.includes("/") || key.includes("-")) && typeof pobjData[key] == 'number') {
            this.marrGridData[pnumIndex][key] = (1 / parrFilterUnitData[0].convfctr) * Number(pobjData['old' + key]);
          }
        })
        console.log(this.marrGridData[pnumIndex]);
        this.msInvConvFact = parrFilterUnitData[0].convfctr.toString();
      } else {
        Object.keys(pobjData).forEach(key => {
          if ((key.includes("/") || key.includes("-")) && typeof pobjData[key] == 'number') {
            this.marrGridData[pnumIndex][key] = parrFilterUnitData[0].convfctr * Number(pobjData['old' + key]);
          }
        })
        console.log(this.marrGridData[pnumIndex]);
        let toStr = (1 / parrFilterUnitData[0].convfctr).toFixed(3);
        this.msInvConvFact = toStr.toString();
      }
     
    }
  }
  bmlfAddRowData() {
    if (!this.bmlfValidDetailData()) {
      return;
    }
    let pobjRowData = Object.assign({}, this.mobjGridRowData);
    let maxLineNo = 0;
    if (this.marrGridData.length == 0) {
      maxLineNo = 1;
    } else {
      const a = this.marrGridData.reduce(function (prev, current) {
        return (prev.RowNum > current.RowNum) ? prev : current
      })
      maxLineNo = a.RowNum + 1;
    }
    pobjRowData.RowNum = maxLineNo;
    pobjRowData.RecDate = null;
    pobjRowData.Status = 'Active';
    this.marrGridData.push(pobjRowData);
  }
  bmlfValidData() {
    if (CustomValidator.bmgfNullCheckValidator(this.mobjForecastModel.ItemKey)) {
      this.bmlfOpenErrorBlock("FORECASTENTRY.MSG.ITEMKEYREQ");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjForecastModel.CalendarKey)) {
      this.bmlfOpenErrorBlock("FORECASTENTRY.MSG.CALENDARKEYREQ");
      return false;
    } else if (this.marrGridData.length == 0) {
      this.bmlfOpenErrorBlock("FORECASTENTRY.MSG.ENTERFORECASTLINE");
      return false;
    } else {
      return true;
    }
  }
  bmlfValidDetailData() {
    let psErrMsg = "";
    for (let i = 0; i < this.marrGridData.length; i++) {
      if (CustomValidator.bmgfNullCheckValidator(this.marrGridData[i].ForecastKey)) {
        psErrMsg = this.utilService.bmgfonTranslate("FORECASTENTRY.MSG.FORECASTKEYREQ");
        break;
      } else if (CustomValidator.bmgfNullCheckValidator(this.marrGridData[i].Location)) {
        psErrMsg = this.utilService.bmgfonTranslate("FORECASTENTRY.MSG.LOCATIONREQ");
        break;
      } else if (CustomValidator.bmgfNullCheckValidator(this.marrGridData[i].Status)) {
        psErrMsg = this.utilService.bmgfonTranslate("FORECASTENTRY.MSG.STATUSREQ");
        break;
      }
    }
    if (psErrMsg != "") {
      this.toastMessageService.notifyError(psErrMsg);
      return false;
    } else {
      return true;
    }
  }
