  bmlfChangeForecast(pstype = null) {
    if (this.mobjForecastModel.CalendarKey != this.msOldCalendarKey || this.mobjForecastModel.ForcastDate != this.msOldForecastDate) {
      let parrFrcstD = [];
      this.marrGridData.forEach((element, key) => {
        let pobj = Object.assign({}, element);
        pobj = this.bmlfSetDataBeforeSave(pobj, key);
        parrFrcstD.push(pobj);
      });
      let pobjReqData: any = {
        JSON: JSON.stringify({
          FrcstH: [{
            ItemKey: this.mobjForecastModel.ItemKey,
            Location: "ALL",
            CalendarKey: this.mobjForecastModel.CalendarKey,
            OldCalendarKey: this.msOldCalendarKey,
            ForecastType: this.mobjForecastModel.ForecastType,
            ForcastDate: moment(this.mobjForecastModel.ForcastDate).format(this.msServeDateFormat)
          }],
          FrcstD: parrFrcstD,
          Response: [{
            ResponseText: '',
            ErrorCode: ''
          }]
        })
      };

      this.formService.bmlfChangeForecast(pobjReqData).subscribe(res => {
        if (res != null && res.FrcstD.length > 0) {
          this.mobjGridRowData = Object.assign({}, res.FrcstD[0]);
          const parrColumnList = Object.keys(this.mobjGridRowData);
          this.bmlfSetGridHeaderData(parrColumnList);
          this.marrGridData = res.FrcstD;
          this.bmlfResetRowData();
          this.msOldCalendarKey = this.mobjForecastModel.CalendarKey;
          this.msOldForecastDate = this.mobjForecastModel.ForcastDate;
          if (pstype == "save") {
            this.mbIsChangeForecastRow = false;
            this.mbIsChangeForecastDate = false;
            this.bmlfSaveForecastEntryData();
          } else {
            this.mbIsChangeForecastRow = false;
            this.mbIsChangeForecastDate = false;
            this.mbIsChangeData = true;
          }
        }
      });
    }
  }
