  bmlfOpenLookupModalCustom(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    
    this.msLookupCallFor = psScreenName;
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      // this.mobjLookupDataModel[psField] = "";
      return false
    }

    let lookuptitle = ''; // For Custom Lookup Caption 17/Apr/2020 Deepak Porwal

    if ((psField.toLowerCase().includes('custom') || psField.toLowerCase().includes('user')) && psField != 'CustomerKey') {
      // Added by deepak porwal on 17/Mar/2020
      let tempData: any;
      if (psScreenName == this.msGrid1Name) {
        tempData = this.marrGridHeaderData.filter(data => {
          return data.Field == psField
        })
        //Begin For Custom Lookup Caption 17/Apr/2020 Deepak Porwal
        if (tempData != undefined) {
          if (tempData.length > 0) lookuptitle = tempData[0]['Title'];
        }
        //End For Custom Lookup Caption 17/Apr/2020 Deepak Porwal
      }

      let json = {
        SqlQuery: tempData[0].SQL
      }
      this.utilService.getCustomSqlData(json).subscribe(res => {
        this.mbIsLoading = false;
        if (res.LookupData.length == 0) {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          return false;
        }
        if (res == null) {
          this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
          return false;
        }

        let obj1: any = {};
        if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
          let psLookupHeading = psField;
          if (psField == "ITEMKEY") {
            psLookupHeading = "ItemKey Lookup";
          };


          if (psLookUpKey == "HeaderLookup") {
            res.LookupData.forEach(ele => {
              ele.RECDATE = moment(ele.RECDATE).format(this.msCustomDateFormat.toUpperCase());
              ele.FROMDATE = moment(ele.FROMDATE).format(this.msCustomDateFormat.toUpperCase());
              ele.TODATE = moment(ele.TODATE).format(this.msCustomDateFormat.toUpperCase());
            });
          }

          this.marrLookupGridData = res.LookupData;
          this.mnumLineItemRowIndex = pnumRowIdx;
          this.mobjLookupKeyData.field = psLookupHeading;
          this.mobjLookupKeyData.gridData = this.marrLookupGridData;
          this.mobjLookupKeyData.type = psType;
          this.mobjLookupKeyData.eleId = psEleId;
          this.mobjLookupKeyData.title = lookuptitle + ' Lookup';   // For Custom Lookup Caption 17/Apr/2020 Deepak Porwal
          if (psValue) {
            this.mobjLookupKeyData.searchValue = psValue;
          } else {
            this.mobjLookupKeyData.searchValue = '';
          }
          if (psEvent == 'blur' && psValue) {
            let parrFilterdArr: any = [];
            let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
            parrFilterdArr = this.marrLookupGridData.filter(obj => {
              if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
                return obj;
              }
            });
            if (parrFilterdArr.length == 1) {
              this.mobjLookupKeyData.value = parrFilterdArr[0];
              this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
              this.bmlfSaveLookupField(this.mobjLookupKeyData);
              this.mbIsLookupModal = false;
            } else {
              this.mbIsLookupModal = true;
            }
          } else if (psEvent == 'click') {
            this.mbIsLookupModal = true;
          }
        }
      })
    }

  }
