import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { CompanyCalendarService } from 'src/app/main/mps-mrp/http/company-calendar-setup.service';
import * as moment from 'moment';

@Component({
  selector: 'app-company-calendar-setup',
  templateUrl: './company-calendar-setup.component.html',
  styleUrls: ['./company-calendar-setup.component.scss']
})
export class CompanyCalendarSetupComponent implements OnInit {
  public mbIsLoading: boolean = false;
  public msScreenName: string = 'Company Calendar Setup';
  public mbIsChangeData: boolean = false;
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public mobjUserData: any = {};
  public marrWeekGridData: any = [];
  public marrYearList: any = [];
  public msYear: string = new Date().getFullYear().toString();
  public marrCalendarList: any = [];
  public mbSaveDialogView: boolean = false;
  public mbIsCloseMode: boolean = false;
  public mbConfirmDialog: boolean = false;
  public mbGenerateDialog: boolean = false;
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/
  constructor(private route: ActivatedRoute,
    public router: Router,
    public utilService: AppUtility,
    private toastMessageService: ToastMessageService,
    private dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    private formService: CompanyCalendarService) { }

  ngOnInit(): void {
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.bmlfGetCompanyCalendarData();
  }
  bmlfGetCompanyCalendarData(pbFlag = "") {
    this.mbIsLoading = true;
    let pobjOData = { HeaderData: [], Response: [] };
    pobjOData.HeaderData.push({
      "Year": this.msYear
    });
    pobjOData.Response.push({
      "ResponseText": "",
      "ErrorCode": ""
    })

    let psJData = JSON.stringify(pobjOData);
    let psTempjData = JSON.stringify(psJData);
    let psJson = '{"JSON":' + psTempjData + '}';
    this.formService.bmlfGetCompanyCalendar(psJson).subscribe(
      res => {
        if (res != null) {
          if (pbFlag == "") {
            this.marrYearList = res.Year;
          }
          if (pbFlag != "save") {
            this.marrWeekGridData = res.HoliCalendar;
          }
          let parrCalData = res.ListCalendar;
          parrCalData.forEach(element => {
            element.Date = new Date(element.Date);
          });
          this.marrCalendarList = parrCalData;
        }
        this.mbIsLoading = false;
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfCloseCompanyCalendar(): void {
    if (this.mbIsChangeData == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      this.router.navigate(["/main/dashboard/dashboardpage"]);
    }
  }
  bmlfSubmitData(): void {
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.mbIsChangeData == false) {
      return;
    }
    this.mbIsLoading = true;
    let psYear = "";
    if (this.marrCalendarList.length > 0) {
      psYear = this.marrCalendarList[0].Date.getFullYear().toString();
    }
    let parrList = [];
    let parrFilter = this.marrCalendarList.filter(x => x.Select == false);
    parrFilter.forEach((element, key) => {
      let pobj = Object.assign({}, element);
      pobj.Date = moment(pobj.Date).format(this.msServeDateFormat)
      parrList.push(pobj);
    });

    let pobjJData = {
      ParaMeters: [{ "Year": psYear }],
      HoliCalendar: parrList,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveCompanyCalendar(pobjJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {

          this.mbIsChangeData = false;
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.router.navigate(["/main/dashboard/dashboardpage"]);
          } else {
            this.bmlfGetCompanyCalendarData("save");
          }
        } else {
          this.mbIsCloseMode = false;
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }
  bmlfGenerateCalendar(): void {
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.mbGenerateDialog = true;
    this.mbConfirmDialog = true;
  }
  bmlfSaveGenerateCalendar() {
    this.mbIsLoading = true;
    let pobjJData = {
      ParaMeters: [{ "Year": "" }],
      HoliCalendar: this.marrWeekGridData,
      Response: [{
        "ResponseText": "",
        "ErrorCode": ""
      }]
    };
    let pobjJson = {
      JSON: JSON.stringify(pobjJData)
    };

    this.formService.bmlfSaveCompanyCalendar(pobjJson).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
         // this.msYear = new Date().getFullYear().toString();
          this.mbIsChangeData = false;
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMPANYCALENDAR.MSG.SAVEGENERATECALENDARMSG"));
          this.bmlfGetCompanyCalendarData("generate");
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }
  bmlfOnConfirmModal(psType) {
    if (psType == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psType == 'NO') {
      this.mbSaveDialogView = false;
      this.router.navigate(['/main/dashboard/dashboardpage']);
    } else {
      this.mbSaveDialogView = false;
      this.mbIsCloseMode = false;
    }
  }
  bmlfGenerateCalendarAction(psType) {
    if (psType == 'Yes') {
      if (this.mbGenerateDialog) {
        this.bmlfSaveGenerateCalendar();
      } else {
        this.bmlfSubmitData();
      }
      this.mbConfirmDialog = false;
    } else if (psType == 'No') {
      this.mbConfirmDialog = false;
      this.mbIsChangeData = false;
      if (!this.mbGenerateDialog) {
        this.bmlfGetCompanyCalendarData("save");
      }
    }
  }
  bmlfChangeYear() {
    if (this.mbIsChangeData) {
      this.mbConfirmDialog = true;
      this.mbGenerateDialog = false;
    } else {
      this.bmlfGetCompanyCalendarData("save");
    }
  }
  bmlfOnFormFieldChange() {
    this.mbIsChangeData = true;
  }
  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/Company Calendar Setup.pdf', '_blank');
  }
  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = "";
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

}
