import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '../../../../../core/services/alert.service';
import { ToastMessageService } from '../../../../../core/services/toast-message.service';
import { AppUtility } from '../../../../../core/services/utility.services';
import { LookupService } from '../../../../../shared/http/lookup.service';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { ModalService } from 'src/app/core/services/modal.service';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { MpsSetupService } from '../../../http/mps-setup.service';
import { MPSSetupModel } from '../../../models/mps-setup.model';

@Component({
  selector: 'app-mps-setup',
  templateUrl: './mps-setup.component.html',
  styleUrls: ['./mps-setup.component.scss']
})
export class MpsSetupComponent implements OnInit {
  public msscreenName: string = 'MPS Setup';
  public mObjModel: MPSSetupModel = new MPSSetupModel();
  public mObjModelBlank: MPSSetupModel = new MPSSetupModel();
  public mobjLookupDataModel: MPSSetupModel = new MPSSetupModel();
  public mbChange: boolean = false;
  public mbShowDialog: boolean = false;
  public msCallFor: string = "";
  public mbloaderEnabled: boolean = false;
  public mbisLookupModal: boolean = false;
  public mbIsLoading: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public mnumLineItemRowIndex: any = null;
  public mbIsLookupModal: boolean = false;
  public msPrecisionFormat: string = '';
  public miPrecision: number = 0;
  public mbMutiSelectionLookup: boolean = false;
  public mLookupDesc: any;
  @ViewChild('custom') customCtrl: any;
  public mbCustomControl = false;

  public mbIsNotesModal: boolean = false;
  public msNotesName: string = "";
  public msNotesValue: string = "";

  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  public mobjUserData: any = {};

  public msLookupCaptionCalendar: string = 'Planning Calendar Lookup';
  public msLookupCaptionUser1: string = 'Demand Fence 1 User Lookup';
  public msLookupCaptionUser2: string = 'Demand Fence 2 User Lookup';
  public msCalendarDescription: string = '';
  public mbisUseMulti: boolean = false;
  public mbisGrpbyLocation: boolean = false;
  public mbisProcessCellOverload: boolean = false;
  public msDfltDRColor: string = 'Black';

  public mbIsHyerLinkRender: boolean = false;
  public marrDrillDownList: any = [];
  public mbHyerLinkData: any = null;
  public msFormMode: string ='I';

  //Esignature
  public mbIsEsignature: boolean = false;
  public mbIsEsignADTPermision: boolean = false;
  public mbIsEsignAuditDialog: boolean = false;
  public mbSignatureModal: boolean = false;
  public mobjSignatureData: any = {};
  public mbCommonAlertDialog: boolean = false;
  public mobjCommonAlertData: any = {};
  public msCustomDateFormat: string = '';
  public mbisConsiderHoldPo=false;

  constructor(private location: Location,
    private objService: MpsSetupService,
    private activeRoute: ActivatedRoute,
    private objAlertService: AlertService,
    private objToastService: ToastMessageService,
    private objUtils: AppUtility,
    private objLookupService: LookupService,
    public router: Router,
    private dateFormatService: DateFormatService,
    private lsService: LocalStorageService,
    private userService: UserFieldService) {
    this.bmlfGetDrillDownList();
  }

  ngOnInit(): void {
    debugger
    this.mobjUserData = this.lsService.getUserSession();
    this.objUtils.getBMEScreePermission(this.msscreenName);
    this.mObjModel.DR1Color = this.msDfltDRColor;
    this.mObjModel.DR2Color = this.msDfltDRColor;
    this.mObjModel.DR3Color = this.msDfltDRColor;
    this.mObjModel.BatchType = 'Mix';
    this.mObjModel.AllowTransferIntermediates = 'Y';
    this.mObjModel.PickDemandDate = 1;
    this.mbisUseMulti = false;
    this.mbisGrpbyLocation = false;
    this.mbisProcessCellOverload = false;
    this.bmlfGetDataOnLoad();
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    //e-Sgnature -s
    this.mobjSignatureData = {
      scrrenName: this.msscreenName,
      reasonMinLength: 10,
      reqSignAuth: 2,
      date: moment().format(this.msCustomDateFormat.toUpperCase()),
      userName: this.mobjUserData.UserId
    };
    this.bmlfCheckEsignApplicableOnForm();
  }

  public bmlfGetDataOnLoad() {
    debugger
    let reqData: any = {
      JSON: JSON.stringify({
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfGetMPSSetup(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        return;
      }
      if (res.HeaderData.length > 0) {
        this.msFormMode = "U";
        this.mObjModel = res.HeaderData[0];
        this.mbisConsiderHoldPo=this.mObjModel.User12;
        this.mbCustomControl = true;
        if (this.mObjModel.UseMultipleEOQ == 'Y') {
          this.mObjModel.UseMultipleEOQ = 1;
          this.mbisUseMulti = true;
        } else {
          this.mObjModel.UseMultipleEOQ = 0;
          this.mbisUseMulti = false;
        }
        if (this.mObjModel.GrpbyLocation == 'Y') {
          this.mObjModel.GrpbyLocation = 1;
          this.mbisGrpbyLocation = true;
        } else {
          this.mObjModel.GrpbyLocation = 0;
          this.mbisGrpbyLocation = false;
        }
        if (this.mObjModel.ProcessCellOverload == 'Y') {
          this.mObjModel.ProcessCellOverload = 1;
          this.mbisProcessCellOverload = true;
        } else {
          this.mObjModel.ProcessCellOverload = 0;
          this.mbisProcessCellOverload = false;
        }
        this.bmlfGetLookupDes(this.mObjModel);
      } else {
        this.mbCustomControl = true;
        this.msFormMode = "I";
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfGetLookupDes(pobjData) {
    let parrSetKeyData = [
      { KeyCode: 'CALENDARKEY', KeyValue1: pobjData.CalendarKey, KeyValue2: '', KeyDescription: '' },
    ];
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName: [{ ScreenName: this.msscreenName }],
        Response: [{ ResponseText: '', ErrorCode: '' }]
      })
    };
    this.userService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      
      this.mLookupDesc = res.LookupData;
      this.mLookupDesc.forEach(element => {
        if (element.KeyCode == 'CALENDARKEY') {
          this.msCalendarDescription = element.KeyDescription;
        }
      });
    });
  }

  //---------------------------------------------------------------------------------------
  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.objUtils.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.objUtils.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msscreenName;
      pobjFormRec.primeKey = '';
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msscreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mbIsEsignADTPermision = res.ESignData[0].ADTPermision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }

  // open e signature modal
  bmlfopenESignatureModal() {
    this.mbSignatureModal = true;
    this.mobjSignatureData.isShow = true;
    this.mobjSignatureData.primaryKey = '';
  }

  // check e signature
  bmlfChkESignature(pobjData) {
    this.mbSignatureModal = pobjData.isShow;
    if (pobjData.event == 'Save') {
      this.mbIsEsignature = false;
      this.mObjModel.ESG_REASON = pobjData.changeReason;
      this.mObjModel.ESG_APPROVER = pobjData.eSignApprover;
      this.mbChange = true;
      this.bmlfonSubmitData();
    } else if (pobjData.event == 'Cancel') {
      this.mobjCommonAlertData = {}
      this.mbCommonAlertDialog = true;
      this.mobjCommonAlertData.message = this.objUtils.bmgfonTranslate('ESIGNREASONMSG');
      this.mobjCommonAlertData.event = 'close';
      this.mbChange = true;
    }
  }

  // ESIGN Audit alert event
  bmlfEsignAuditAlertEvent(event) {
    if (event == 'No') {

    } else {
      this.mbIsEsignAuditDialog = false;
    }
  }
  // CommonAlertDialog
  bmlfmbCommonAlertDialog(event) {
    this.mbCommonAlertDialog = false;
  }

  bmlfViewHelpDoc() {
    window.open('../../../../../../assets/help files/MPS Setup.pdf', '_blank');
  }

  bmlfonClose() {
    debugger
    if (!this.mbChange && !this.customCtrl.mbChange) {
      this.router.navigate(['/main/dashboard/dashboardpage']);
    }
    else {
      this.mbShowDialog = true;
      this.msCallFor = "FormClose";
    }
  }

  //Lookup
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mbMutiSelectionLookup = false;

    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      this.mbIsLoading = false;
      return false;
    }

    let psReqData: any;
    if (psLookUpKey == "PlanningCalendar") {
      psReqData = { "ScreenName": this.msscreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };

    };

    this.objLookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      let obj1: any = {};
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach(ele => {
          if (psLookUpKey == "PlanningCalendar") {
            ele.RecDate = moment(ele.RecDate).format(this.msCustomDateFormat.toUpperCase());
          }
        });
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          let pobjFirstInput;
          pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];

          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfopenCustomLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mbMutiSelectionLookup = true;
    this.mbIsLoading = true;
    let psDBName = JSON.parse(localStorage.getItem('usersession')).CmpDbName;
    let pobjReqData = {
      ScreenName: 'MPSSetupComponent',
      LookUpKey: 'UserID',
      Filter1: psDBName
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        let parrValue = [];
        // parrValue =psValue.
        if (psValue) {
          this.mobjLookupKeyData.searchValue = '';//psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        // Bug 189790
        let parrSplitUser:any = "";
        if(psField == this.msLookupCaptionUser1){
        parrSplitUser = this.mObjModel.User1.split(",");
        }
        else if (psField == this.msLookupCaptionUser2){
        parrSplitUser = this.mObjModel.User2.split(",");
        }
        if (parrSplitUser.length > 0) { 
          res.LookupData.forEach(element => {
            let parrFilterData = parrSplitUser.filter(x => x == element.User_ID);
            if (parrFilterData.length > 0) {
              element.checked = true;
            }
          });
        }
        if (psEvent == 'blur' && psValue) {
          let inputKey = psField;
          let filterdArr: any = [];
          let firstInput = Object.keys(this.marrLookupGridData[0])[0];
          filterdArr = this.marrLookupGridData.filter(obj => obj[firstInput].toUpperCase().trim() == psValue.toUpperCase());
          if (filterdArr.length == 1) {
            this.mobjLookupKeyData.value = filterdArr[0];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        }
        else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      }
      else {
        this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.LOOKUPNODATA'));
      }
    }, err => {
      console.log(err);
    });
  }

  bmlfisUserExist(ExistingUser, UserID) {
    if (ExistingUser.length > 0) {
      var splitArray = ExistingUser.split(",");
      for (let i = 0; i < splitArray.length; i++) {
        if (splitArray[i] == UserID) {
          return true;
          break;
        }
      }
    }
    return false;
  }

  bmlfSaveLookupField(pobjEvent) {
    this.mbIsLookupModal = false;
    if (pobjEvent.field == this.msLookupCaptionCalendar) {
      this.mObjModel.CalendarKey = pobjEvent.value["CalendarKey"].trim();
      this.msCalendarDescription = pobjEvent.value["Description"].trim();
    } 
    // // else if (pobjEvent.field == this.msLookupCaptionUser1) {
    // //   //this.mObjModel.User1 = '';
    // //   pobjEvent.value.forEach(element => {
    // //     if (!this.bmlfisUserExist(this.mObjModel.User1, element.User_ID)) {
    // //       if (this.mObjModel.User1 == '') {
    // //         this.mObjModel.User1 = element.User_ID;
    // //       } else {
    // //         this.mObjModel.User1 = this.mObjModel.User1 + ',' + element.User_ID;
    // //       }
    // //     }
    // //   });

    // // } else if (pobjEvent.field == this.msLookupCaptionUser2) {
    // //   //this.mObjModel.User2 = '';
    // //   pobjEvent.value.forEach(element => {
    // //     if (!this.bmlfisUserExist(this.mObjModel.User2, element.User_ID)) {
    // //       if (this.mObjModel.User2 == '') {
    // //         this.mObjModel.User2 = element.User_ID;
    // //       } else {
    // //         this.mObjModel.User2 = this.mObjModel.User2 + ',' + element.User_ID;
    // //       }
    // //     }
    // //   });
    // // }
    ////// Bug 189790 
    else if (pobjEvent.field == this.msLookupCaptionUser1) {
      let psTempUser: string = "";
      pobjEvent.value.forEach(element => {
          if (psTempUser == '') {
                psTempUser = element.User_ID;
          } else {
                psTempUser = psTempUser + ',' + element.User_ID;
          }
      });
        this.mObjModel.User1 = psTempUser;
    } 
    else if (pobjEvent.field == this.msLookupCaptionUser2) {
      let psTempUserTwo: string = "";
      pobjEvent.value.forEach(element => {
        if (psTempUserTwo == '') {
          psTempUserTwo = element.User_ID;
        } else {
          psTempUserTwo = psTempUserTwo + ',' + element.User_ID;
        }
      });
    }
    this.bmlfonChange();
  }

  bmlfonChange() {
    this.mbChange = true;
    if (this.mObjModel.CalendarKey == '') {
      this.msCalendarDescription = '';
    }
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate(psMsg));
  }

  // Set Lookup Value Column
  bmlfGetInputColumn(psfield) {
    if (psfield == this.msLookupCaptionCalendar) {
      return 'CalendarKey';
    }
  }
  //Return Model Field
  bmlfGetModelField(psfield, rowIndex) {
    if (psfield == this.msLookupCaptionCalendar) {
      return 'CalendarKey';
    }
  }

  bmlfCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
    if (pobjData.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjData.field);
      let parrFilterData: any = [];
      if (psInputKey && pobjData.searchValue != '') {
        parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0 || pobjData.searchValue == "") {
        let psModelField = this.bmlfGetModelField(pobjData.field, pobjData.eleId);
        this.mObjModel[psModelField] = '';
        this.bmlfonChange();
        // if(pobjData.eleId == 'txtUser1'){
        //   this.mObjModel.User1 =''
        // }else if(pobjData.eleId == 'txtUser2'){
        //   this.mObjModel.User2 =''
        // }
      }
    }
  }

  //Save
  bmlfonValidateFormData() {
    if (this.mObjModel.CalendarKey == '') {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('MPSSETUP.MSG.CALENDARKEY')));
      setTimeout(function () { document.getElementById('txtCalendarKey').focus(); }, 10);  //Avanish 8May20 SCR:134599
      return false;
    }
    return true;
  }

  bmlfonSave() {
    //check Write permission
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    let pbResult: boolean = this.bmlfonValidateFormData();
    if (pbResult == false) {
      return;
    }

    if (!this.mbChange && !this.customCtrl.mbChange) {
      return;
    };
    this.msCallFor = "Save";
    this.bmlfonSubmitData();
  }

  bmlfonSubmitData() {    
    //check Write permission
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (!this.mbChange && this.customCtrl.mbChange == false) {
      return;
    };

    let pbResult: boolean = this.bmlfonValidateFormData();
    if (pbResult) {
      //Save e-Signature function -S
      if (this.mbIsEsignature) {
        this.bmlfopenESignatureModal();
        return false;
      }
      //Save e-Signature Function -E

      let data = this.objUtils.bmgfCustomModelTransform(this.customCtrl);
      this.bmlfonAssignCustValues(data);
      if (this.customCtrl != undefined) {
        if (!this.customCtrl.bmgIsMandatary(this.mObjModel)) {
          return;
        }
    }
      this.mObjModel.User12=this.mObjModel.User12==true ? 1 : 0;
      let pobjModel = Object.assign({}, this.mObjModel);

      

      if (this.mObjModel.UseMultipleEOQ == 1) {
        pobjModel.UseMultipleEOQ = 'Y';
      } else {
        pobjModel.UseMultipleEOQ = 'N';
      }
      if (this.mObjModel.GrpbyLocation == 1) {
        pobjModel.GrpbyLocation = 'Y';
      } else {
        pobjModel.GrpbyLocation = 'N';
      }
      if (this.mObjModel.ProcessCellOverload == 1) {
        pobjModel.ProcessCellOverload = 'Y';
      } else {
        pobjModel.ProcessCellOverload = 'N';
      }
      // if (this.mObjModel.PickDemandDate == 1) {
      //   pobjModel.OptDemandDate = 'Target Ship';
      // } else if (this.mObjModel.PickDemandDate == 0) {
      //   pobjModel.OptDemandDate = 'REQUESTED';
      // } else if (this.mObjModel.PickDemandDate == 2) {
      //   pobjModel.OptDemandDate = 'Promise';
      // }
      //pobjModel.Difference = this.mObjModel.User11;
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{ ...pobjModel }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfSaveMPSSetup(reqData).subscribe(res => {
        if (res == null) {
          this.objToastService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.SAVE'));
          if (this.mbHyerLinkData != null) {
            this.bmlfRedirectFunctionality();
          }
          if (this.msCallFor == "Save") {
            this.mbChange = false;
            this.customCtrl.mbChange = false;
            this.mbShowDialog = false;
            this.bmlfGetDataOnLoad();
          }
          if (this.msCallFor == "FormClose") {
            this.router.navigate(['/main/dashboard/dashboardpage']);
          }
        }
        else {
          if (this.objUtils.bmgffindString(res.Response[0].ResponseText, 'EXIST')) {
            this.objToastService.notifyError(this.objUtils.bmgfonTranslate('COMMON.MSG.EXIST'));
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
          }
          this.mbShowDialog = false;
        }
      })
    }
    this.mbShowDialog = false;
  }

  bmlfonDialogAction(action) {
    debugger
    if (action == 'No') {
      this.mbShowDialog = false;
      this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        this.router.navigate(['/main/dashboard/dashboardpage']);
      } else {
        return;
      }
    } else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    } else if (action == 'Yes') {
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msCallFor = '';
    }
    else {
      this.mbShowDialog = false;
    }
  }

  //---------------------------------------------------------------------------------------
  bmlfonAssignCustValues(custom) {
    this.mObjModel.CUSTOM1 = custom.CUSTOM1 ? custom.CUSTOM1 : 0;
    this.mObjModel.CUSTOM2 = custom.CUSTOM2 ? custom.CUSTOM2 : 0;
    this.mObjModel.CUSTOM3 = custom.CUSTOM3 ? custom.CUSTOM3 : 0;
    this.mObjModel.CUSTOM4 = custom.CUSTOM4 ? custom.CUSTOM4 : 0;
    this.mObjModel.CUSTOM5 = custom.CUSTOM5 ? custom.CUSTOM5 : 0;
    this.mObjModel.CUSTOM6 = custom.CUSTOM6 ? custom.CUSTOM6 : 0;
    this.mObjModel.CUSTOM7 = custom.CUSTOM7 ? custom.CUSTOM7 : 0;
    this.mObjModel.CUSTOM8 = custom.CUSTOM8 ? custom.CUSTOM8 : 0;
    this.mObjModel.CUSTOM9 = custom.CUSTOM9 ? custom.CUSTOM9 : 0;
    this.mObjModel.CUSTOM10 = custom.CUSTOM10 ? custom.CUSTOM10 : 0;
  }

  //Hyperlink
  bmlfGetDrillDownList() {
    let pobjReqData = {
      ScreenName: 'ApplyPaymentComponent',
      LookUpKey: 'DrillDwnList',
      Filter1: 'MPS Setup'
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.marrDrillDownList = res.LookupData;
        this.mbIsHyerLinkRender = true;
      }
    });
  }
  // DrillDown emit event----------------------------------
  bmgfDrillDownEvent(pobjData: any) {
    this.mbHyerLinkData = pobjData;
    this.bmlfonSave();
  }

  bmlfRedirectFunctionality() {
    if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.User1)) {
      let queryParamsObj = { queryParams: {} };
      queryParamsObj.queryParams[this.mbHyerLinkData.data.User2] = this.mbHyerLinkData.msField1Value;
      if (!CustomValidator.bmgfNullCheckValidator(this.mbHyerLinkData.data.KeyValue2)) {
        queryParamsObj.queryParams[this.mbHyerLinkData.data.User3] = this.mbHyerLinkData.msField2Value;
      }
      this.router.navigate([this.mbHyerLinkData.data.User1], queryParamsObj);
    }
  }

  // Open Notes
  bmlfOpenNotesModal(psName: string, psValue: string) {
    if (!this.isEmpty(psValue)) {
      this.mbIsNotesModal = true;
      this.msNotesName = psName;
      this.msNotesValue = psValue;
    } else {
      //Key cannot be empty
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.NOTES'));
    }
  }
  bmlfCloseNotesModal(pobjEvent: any): void {
    this.mbIsNotesModal = pobjEvent.option;
  }
  isEmpty(value) {
    return (typeof value === "undefined" || value === null || value === '');
  }

}
