import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class PlanningCalendarService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeletePlanningCalendar(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeletePlanningCalendar`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSavePlanningCalendar(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFCreatePlanningCalendar`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdatePlanningCalendar`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetPlanningCalendar(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetPlanningCalendar`;
        return this.httpExt.post<any>(url, headerData, null);
    }
}