import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { ForecastEntryListComponent } from './components/mps/forecast-entry/forecast-entry-list/forecast-entry-list.component';
import { ForecastEntryManageComponent } from './components/mps/forecast-entry/forecast-entry-manage/forecast-entry-manage.component';
import { MaintainMpsItemsComponent } from './components/mps/maintain-mps-items/maintain-mps-items.component';
import { MpsPlanningWorksheetComponent } from './components/mps/mps-planning-worksheet/mps-planning-worksheet.component';
import { PlanningCalendarComponent } from './components/mps/Planning-calendar/planning-calendar-list/planning-calendar-list.component';
import { PlanningCalendarManageComponent } from './components/mps/Planning-calendar/planning-calendar-manage/planning-calendar-manage.component';
import { RunMpsComponent } from './components/mps/run-mps/run-mps.component';
import { MpsSetupComponent } from './components/mps/mps-setup/mps-setup.component';
import { MpsMrpComponent } from './mps-mrp.component';
import { CompanyCalendarSetupComponent } from './components/mps/company-calendar-setup/company-calendar-setup.component';
import { EoqListComponent } from './components/mps/economic-order-quantity/eoq-list/eoq-list.component';
import { EoqManageComponent } from './components/mps/economic-order-quantity/eoq-manage/eoq-manage.component';
import { RunMrpComponent } from './components/mrp/run-mrp/run-mrp.component';
import { MrpDashboardComponent } from './components/mrp/mrp-dashboard/mrp-dashboard.component';
import { ForecastImportExportComponent } from './components/mps/forecast-import-export/forecast-import-export.component';

const routes: Routes = [{
  path: '',
  component: MpsMrpComponent,
  children: [{
    path: '', redirectTo: 'mpsmrp', pathMatch: 'full'
  }, {
    path: 'planning-calendar', component: PlanningCalendarComponent, canActivate: [AuthGuard]
  }, {
    path: 'maintain-mps-items', component: MaintainMpsItemsComponent, canActivate: [AuthGuard]
  }, {
    path: 'planning-calendar/manage', component: PlanningCalendarManageComponent, canActivate: [AuthGuard]
  }, {
    path: 'forecast-entry', component: ForecastEntryListComponent, canActivate: [AuthGuard]
  }, {
    path: 'forecast-entry/manage', component: ForecastEntryManageComponent, canActivate: [AuthGuard]
  }, {
    path: 'mps-dashboard', component: MpsPlanningWorksheetComponent, canActivate: [AuthGuard]
  }, {
    path: 'run-mps', component: RunMpsComponent, canActivate: [AuthGuard],
  }, {
    path: 'mps-setup', component: MpsSetupComponent, canActivate: [AuthGuard],
  }, {
    path: 'company-calendar-setup', component: CompanyCalendarSetupComponent, canActivate: [AuthGuard],
  }, {
    path: 'economic-order-quantity', component: EoqListComponent, canActivate: [AuthGuard]
  }, {
    path: 'economic-order-quantity/manage', component: EoqManageComponent, canActivate: [AuthGuard]
  }, {
    path: 'run-mrp', component: RunMrpComponent, canActivate: [AuthGuard],
  }, {
    path: 'mrp-dashboard', component: MrpDashboardComponent, canActivate: [AuthGuard],
  }, {
    path: 'forecast-import-export', component: ForecastImportExportComponent, canActivate: [AuthGuard],
  }
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MpsMrpRoutingModule { }
